.form {
    display: flex;
    flex-direction: column;
    align-items: center;
  
    margin: 0 auto;
  
    width: 320px;
    border: 2px solid black;
    padding: 15px 25px;
    border-radius: 7px;
  }
  
  .label {
    display: flex;
    flex-direction: column;
    width: 100%;
  
    font-size: 12px;
    text-transform: uppercase;
    color: #a2a2a2;
  }
  
  .input {
    display: flex;
    margin: 5px 0 20px;
    width: 100%;
    height: 35px;
    border-radius: 5px;
    border: 1px solid black;
  
    font-size: 16px;
    padding: 5px 7px;
  }
  
  .submit_btn {
    padding: 10px 30px;
    border-radius: 5px;
    font-size: 13px;
    text-transform: uppercase;
    cursor: pointer;
  
     /* &:hover {
     background: #e2e2e2;
    } */
  }

  .submit_btn:hover {background: #e2e2e2;}
  